.slider {
    background: url("../images/bg/banner.jpg") no-repeat;
    background-size: cover;
    background-position: 10% 0%;
    padding: 100px 0;
    position: relative;
    @extend .overly;
    .block {
        h1 {
            font-size: 80px;
            line-height: 80px;
            color: $light;
        }
        p {
            color: #b9b9b9;
        }
    }
}

@include mobile-xs {
    .slider .block h1 {
        font-size: 41px;
        line-height: 57px;
    }
}

@include mobile-xs {
    .slider .block h1 {
        font-size: 28px;
        line-height: 40px;
    }
    .slider {
        padding: 126px 0 151px 0;
    }
}

@include mobile {
    .slider .block h1 {
        font-size: 38px;
        line-height: 50px;
    }
    .slider {
        padding: 126px 0 151px 0;
    }
}

@include tablet {
    .slider .block h1 {
        font-size: 56px;
        line-height: 70px;
    }
}

.carousel-section {
    max-width: 110rem;
    /* Adjust the maximum width as per your requirements */
    margin: 0 auto;
    top: 0 !important;
    left: 0 !important;
    background-image: url("/images/bg/love-peace-unity.jpg");
}

.carousel-inner .carousel-item img {
    width: 90%;
    /* Adjust the width of the images */
    height: 550px;
    /* Adjust the height of the images */
    object-fit: cover;
    /* Maintain the image aspect ratio and cover the container */
}

.carousel-caption {
    top: 70%;
    text-align: center;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(0, 0, 0, 0.2);
    max-width: 600px;
    position: absolute !important;
    display: block;
    // align-items: center;
    color: white !important;
}