/*=================================================================
  Latest Posts
==================================================================*/

.blog-item-content {
    h3 {
        line-height: 36px;
    }
    h3 a {
        transition: all .4s ease 0s;
        &:hover {
            color: $primary-color !important;
        }
    }
}

.blog-item {
    background: $light;
    transition: all .4s ease 0s;
    border: 1px solid rgba(0, 0, 0, 0.05);
    .card-body {
        padding: 25px;
    }
    &:hover {
        box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .15) !important;
    }
}

//  Blog Main
.tags {
    display: block !important;
    a {
        background: #f5f8f9;
        display: inline-block;
        padding: 8px 23px;
        border-radius: 38px;
        margin-bottom: 10px;
        border: 1px solid #eee;
        font-size: 14px;
        text-transform: capitalize;
        &:hover {
            background-color: $primary-color;
            border-color: $primary-color;
            color: white;
        }
    }
}

.pagination .nav-links a {
    font-size: 18px;
    border: 2px solid rgba(0, 0, 0, 0.5);
    display: inline-block;
    width: 50px;
    height: 50px;
    text-align: center;
    padding-top: 10px;
    border-radius: 3px;
    &:hover {
        background: $secondary-color;
        border-color: $secondary-color;
        color: $light;
    }
}

.pagination .nav-links a.current {
    background: $secondary-color;
    border-color: $secondary-color;
    color: $light;
}

// Single Post Page
// 
.blog-item-meta {
    span {
        text-transform: uppercase;
        font-size: 14px;
    }
}

.comment-area-box {
    img {
        width: 100px;
        height: 100px;
        border-radius: 3px;
        margin-bottom: 10px;
    }
    @include mobile {
        display: block !important;
    }
}

.comment-form {
    .input {
        background: #f7f9f3;
        height: 55px;
        padding-left: 10px;
        border: 1px solid rgba(0, 0, 0, 0.04);
    }
    textarea.input {
        height: auto;
    }
}

.share-option {
    a {
        display: inline-block;
        padding: 8px 22px;
        font-size: 14px;
        background: #f7f9f3;
        border-radius: 25px;
        margin: 10px 0;
        &:hover {
            background: $primary-color;
            color: $light;
        }
    }
}

h3.quote {
    font-size: 25px;
    line-height: 50px;
    font-weight: normal;
    padding: 0px 25px 0px 85px;
    margin: 65px 0 65px 0 !important;
    position: relative;
    @include mobile {
        padding: 0px 25px 0px 25px;
    }
}

h3.quote::before {
    content: '';
    width: 55px;
    height: 2px;
    background: $primary-color;
    position: absolute;
    top: 25px;
    left: 0;
    @include mobile {
        top: 0;
        width: 2px;
        height: 55px;
    }
}

.nav-posts-title {
    line-height: 25px;
    font-size: 18px;
}

.mt-70 {
    margin-top: -70px;
}

.border-1 {
    border: 1px solid rgba(0, 0, 0, 0.05);
}

.blog-item {
    border-bottom: 1px solid rgba(0, 0, 0, 0.05)
}

@include mobile-xs {
    .blog-item-meta span {
        margin: 6px 0px;
    }
    .widget {
        margin-bottom: 30px;
        padding-bottom: 0px;
    }
}

@include mobile {
    .blog-item-meta span {
        display: block;
        margin: 6px 0px;
    }
    .widget {
        margin-bottom: 30px;
        padding-bottom: 0px;
    }
}

@include tablet {
    .blog-item-meta span {
        display: block;
        margin: 6px 0px;
    }
    .widget {
        margin-bottom: 30px;
        padding-bottom: 0px;
    }
}

.overlay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.popup {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    max-width: 80%;
    width: 100%;
    text-align: center;
}

.close-btn {
    cursor: pointer;
    color: #333;
    font-size: 18px;
    font-weight: bold;
    position: absolute;
    top: 10px;
    right: 10px;
}

button {
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
}


/* Media Query for smaller screens */

@media screen and (max-width: 600px) {
    .popup {
        max-width: 90%;
    }
}

