#navbar {
    @include desktop {
        padding: 8px 0;
        .navbar-menu {
            padding: .4rem 0;
        }
    }
    .navbar-burger {
        padding-left: 12px;
        border: 0;
        margin-top: 11px;
        margin-left: 120px;
        color: #fff;
        background-color: $primary-color;
        outline: 0;
        height: 2.8rem;
        width: 2.8rem;
        span {
            left: calc(49% - 7px);
            &:nth-child(1) {
                top: calc(50% - 5px)
            }
            &:nth-child(2) {
                top: 50%
            }
            &:nth-child(3) {
                top: calc(50% + 5px)
            }
        }
        &.is-active {
            background-color: #282b40;
        }
    }
    li.navbar-item {
        padding: 0;
        &:last-child .navbar-link {
            padding-right: 0;
            @include desktop {
                //margin-right: 17px !important;
            }
        }
    }
    .navbar-link {
        display: flex;
        justify-content: flex-start;
        font-family: $secondary-font;
        color: $black;
        display: flex;
        padding-left: 0px;
        margin: 0 5px 10px 10px;
        background-color: transparent !important;
        text-transform: capitalize;
        font-weight: 800;
        padding: 10px 7px 0 30px;
        transition: all .25s ease;
        @include large-desktop {
            padding: 3px 14px;
            font-size: 15px;
        }
        @include desktop {
            // padding: 10px 14px;
            text-align: center;
        }
        &:hover,
        &:focus {
            color: $primary-color;
        }
    }
    .btn {
        &:hover {
            color: $light;
        }
    }
}

// .navbar-menu {
