.header-top {
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    font-size: 14px;
    @include desktop {
        padding: 6px 0;
    }
    .top-btn {
        padding: 14px 25px;
        background: $primary-color;
        line-height: 40px;
        color: $light;
        @include mobile-xs {
            padding: 8px 13px;
            font-size: 11px;
            display: block;
            line-height: unset;
            margin-left: 0 !important;
            margin-top: 10px !important;
        }
        &:hover {
            background: $secondary-color;
        }
    }
    .header-top-right {
        a {
            margin: 0 10px;
            &:first-child {
                margin-left: 0;
                @include mobile-xs {
                    margin-left: 10px;
                }
            }
            &:last-child {
                margin-right: 0;
            }
        }
    }
    .header-top-info {
        a {
            margin-right: 20px;
            color: #000;
            @include mobile-xs {
                display: block;
                margin-right: 0 !important;
            }
            i {
                color: $primary-color;
            }
        }
    }
    .btn {
        font-size: 12px !important;
        margin-right: 0px;
    }
}

.navbar-brand {
    padding: 12px 0 0 45px;
    img {
        max-height: 100px;
        display: flex;
        //padding-left: 0 !important;
    }
    @include desktop {
        .navbar-item {
            padding-left: 0;
        }
    }
    .logo-sm {
        display: none;
        /* Hide both logos by default */
    }
    @include desktop {
        justify-content: flex-start;
        .navbar-item {
            padding-left: 0 !important;
        }
        .logo-lg {
            display: none;
            /* Hide both logos by default */
        }
        .logo-sm {
            display: flex;
            //padding-right: 12rem;
            max-height: 135px;
            //padding: 12px;
            /* Show the small screen logo */
        }
    }
}

.navbar-dropdown {
    visibility: hidden;
    opacity: 0;
    transition: all .2s ease-in, visibility 0s linear .2s, transform .2s linear;
    width: 20px;
    padding: 0px;
    border-radius: 0px;
    display: block;
    border: 0;
    z-index: -1;
    &.align-right {
        left: auto;
        right: -34px;
    }
    @include desktop {
        display: none;
        visibility: visible;
        opacity: 1;
        margin: auto;
        text-align: center;
        background: #f5f5f5;
        width: 250px;
        border: 1px solid rgba(0, 0, 0, 0.03);
        border-bottom: 0;
    }
}

.navbar-link::after {
    display: none !important;
}

.has-dropdown:hover .navbar-dropdown {
    visibility: visible;
    transition: all .45s ease 0s;
    opacity: 1;
    z-index: 3;
}

.navbar-dropdown .navbar-item {
    padding: .8rem 1.5rem;
    font-size: 14px;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.7);
    border-bottom: 1px solid rgba(0, 0, 0, 0.03);
    &:hover {
        background: $primary-color !important;
        color: $light !important;
    }
    @include desktop {
        padding: .6rem 1.5rem;
    }
}

@include tablet {
    .header-top .header-top-info a:last-child {
        margin-right: 0px;
    }
    .header-top .header-top-info {
        padding: 10px 0px;
    }
    .header-top-right {
        margin-bottom: 12px;
    }
    .dropdown-menu {
        display: none;
        width: 100%;
        text-align: center;
    }
    #navbar .nav-link {
        padding: 5px;
    }
    // .navbar-item {
    //     display: block;
    //     //padding: 0 150px 0px 0;
    // }
}

